const clearTimer = timer => {
	if (timer) {
		clearInterval(timer);
	}
};

const registerTimerAction = {
	effect(coeffects) {
		const {
			state: {timer},
			properties: {interval},
			updateState
		} = coeffects;

		// unregister previous timer handler, if exists
		clearTimer(timer);

		const newTimer = setInterval(function() {
			updateState({tick: Date.now()});
		}, interval);

		updateState({timer: newTimer});
	}
};

const unregisterTimerAction = {
	effect(coeffects) {
		const {
			state: {timer}
		} = coeffects;

		clearTimer(timer);
	}
};

const effectHandlers = {
	registerTimerAction,
	unregisterTimerAction
};

export default {
	effectHandlers
};