export default {
	RELATED_ITEMS_LIST_INIT: 'SN_OPWS_RELATED_ITEMS_LIST#INIT',

	/**
	 * Triggered when the cards panel should be refreshed, means the `items` array should be fetched again from the server.
	 * Payload:
	 * `selectedType`
	 * `selectedSortOption` id of sort option that can be found on selectedType.sortOptions
	 * `firstRecord` - offset for the query
	 * `forceRefresh` - boolean, indicates that data should be bring for the latest timestamp.
	 * Otherwise (if false), the data should be aligned with the timestamp fro mteh previous request (paging freeze mode).
	 * Maintaining the timestamp from the previous REFRESH request is specific t oeach request logic and should be maintained on the using app side.
	 */
	REFRESH_REQUESTED: 'OPWS_RELATED_ITEMS_LIST#REFRESH_REQUESTED',

	/**
	 * Dispatched by card when it has been seleced or un-selected by the user interaction.
	 * `isSelected` - current state: true - if card was seleceted, false - if it was unselected.
	 * `id` - card/item unique id
	 * `typeId` - id of currently selected type
	 */
	CARD_SELECTED: 'NOW_CARD#SELECTED_SET',

	/**
	 * Dispatched when user changes the sorting order of the alerts
	 * Payload
	 * 	selectedSortOption - the seleced Id of the option within the dropdown
	 */
	SORT_SELECTED: 'OPWS_RELATED_ITEMS_LIST#SORT_SELECTED',


	/**
	 * Dispatched when 'back' button is clicked on the header.
	 * Default payload is empty object.
	 * Use `append-to-payload` to add payload.
	 */
	BACK_ACTION: 'OPWS_RELATED_ITEMS_LIST#BACK_ACTION'
};