import {identity as _identity} from 'lodash';
import {MISC} from './utils.js';

const iterateProps = (data, key) => 
	deepFreeze(data[key]);

/**
 * This is internal sanity function that recursively freezes the given object.
 * Any attempt to modify any nested property on frozed object will throw an expcetion.
 * Use this function to protect against accidential mutability of your collections.
 * The fucntion is turned off in PROD environment to eliminate any overhead. Means, use it and keep it in your code, it won't hurt.
 * @param data - an object to turn into immutable.
 * `data` will turn into immutable.
 * @return the input `data` object.
 * 
 */
export const deepFreeze = MISC.isInDev() ? 
	data => {
		if (!data || typeof data !== 'object')
			return data;

		const ownProps = Object.getOwnPropertyNames(data);
		ownProps.forEach(iterateProps.bind(null, data));
		return Object.freeze(data);
	} : 
	_identity;