import {PERFORMANCE} from '../utils';

export const extendWithPerformance = (name, baseConfig, options = {}) => {
	return {
		...baseConfig,
		renderer: {
			view: viewWrapper(baseConfig.renderer.view)
		},
		initialState: {
			...baseConfig.initialState,
			extensions: {
				performance: {
					allow: true,
					suffix: name,
					...options
				},
				...baseConfig.extensions
			}
		}
	};
};

const viewWrapper = view => (function(state, dispatch) {
	return PERFORMANCE.measureCall(view, [state, dispatch], true, state.extensions.performance);
});
