import Color from 'color';
import {forEach as _forEach, memoize as _memoize} from 'lodash';

/**
 * Convert hex color code to R,G,B tuple for var value.
 * @param {*} hex 
 */
export function hexToRGB(hex) {
	return Color(hex)
		.rgb()
		.array();
}

/**
 * A11y utility.
 * Returns true if given background should have white text on it.
 * If false, the text expected to be black.
 * @param {String} bgColor - current background color.
 * @return {Boolean} true if foreground should be inverted to white, false if foreground should stay black.
 */
export const shouldUseWhiteText = _memoize(bgColor => {
    const luminosity = Color(bgColor).luminosity();

    // The minimal contrast ratio for WCAG AA should be 4.5:1.
    // https://webaim.org/articles/contrast/
    let contrastRatioToBlack = (0.05 + luminosity) / 0.05;
    if (contrastRatioToBlack < 1.0) 
        contrastRatioToBlack = 1.0 / contrastRatioToBlack;

    return contrastRatioToBlack < 4.5;
});

