import {sleep} from './sleep';

/**
 * Utility function.
 * Synchronous version.
 * Invokes provided `fn` in next animation frame.
 * Useful when accessing element inside `hook-insert` callback, 
 * or other callback that is sensitive to DOM state.
 * @param {Function} fn 
 */
export const invokeOnNextFrame = fn => requestAnimationFrame(() => setTimeout(fn, 10));

/**
 * Utility function.
 * Asynchronous version.
 * Use `await onNextFrame()` in your async function to wait for next animation frame.
 */
export const onNextFrame = async () => 
	new Promise(resolve =>
		requestAnimationFrame(async () => {
			await sleep(10);
			resolve();
		})
	);