const ACTION_TYPES = {
    TOGGLE_SIDE_PANEL: 'NOW_RECORD_COMMON_SIDEBAR#OPENED_SET'
};

const toggleSidePanel = coeffects => {
    const {
        action: {
            payload: {
                value = false,
                sidePanelStatePropertyName = ''
            }
        },
        updateProperties,
    } = coeffects;

    updateProperties({[sidePanelStatePropertyName]: value});
};

const effectHandlers = {
    toggleSidePanel
};

export default {
    ACTION_TYPES,
    effectHandlers
}