import {createCardComponent, generateViewFn} from '../../cardComponentCreator/createCardComponent';
import {getAlertCardContentJsx as getCardContentJsx} from './getAlertCardContentJsx';
import {ACTION_TYPES} from './actionTypes';
import styles from './snOpwsAlertCard.scss';

const view = generateViewFn({
	getCardContentJsx,
	openFormActionType: ACTION_TYPES.OPEN_ALERT_FORM_INVOKED,
	cardSelectedActionType: ACTION_TYPES.ALERT_CARD_SELECTED,
	footerFieldName: 'lastRemoteTime'
});

/**
 * Seismic component `sn-opws-alert-card`.
 * Reusable card for alert item to show on side panel on nocpit and on workspace map.
 **/
createCardComponent({
	componentName: 'sn-opws-alert-card',
	styles,
	view
});