import {createActionConfig} from '../../createActionConfig';
import {createNocElement} from '../../createNocElement';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import effectIsVisible from './effectIsVisible';

/**
 * This component is used to detect when a DOM is hidden or visible on the screen.
 * The component performs check on passed DOM ref `anchorElm` once per 1 second and dispatches event `actionOnChange`,
 * when element state changes from hidden to visible or vice versa. 
 * The payload of `actionOnChange` contains boolean `isVisible`.
 * 
 * Because of 1 second interval, it may happen that element flipped isVisible state twice in less than 1 second, and this component will miss the change.
 * This side effect is considered as positive, it debounces user interactions that were reverted in a very short time.
 * 
 * The existence and the usage of this component serves currently as a workaround for lack of action from `now-record-common-sidebar-connected`,
 * that would notify about switching the tab or toggling the entire panel visibility.
 */
createNocElement('sn-opws-is-visible', {
	renderer: {
		type: snabbdom,
		view() {
			return null;
		}
	},

	initialState: {
		isVisible: false, //visibility state
		visibilityInterval: null //interval id for clearInterval
	},

	properties: {
		anchorElm: {
			default: null //ref to DOM element
		},

		actionOnChange: {
			default: effectIsVisible.ACTION_TYPES.DEFAULT_ON_CHANGE
		}
	},

	actionHandlers: {
		[effectIsVisible.ACTION_TYPES.START_CHECKS]:
			createActionConfig(effectIsVisible.effectHandlers.startChecking),

		[effectIsVisible.ACTION_TYPES.CHECK_IF_VISIBLE]:
			createActionConfig(effectIsVisible.effectHandlers.checkIfVisible),

		[effectIsVisible.ACTION_TYPES.STOP_CHECKS]:
			createActionConfig(effectIsVisible.effectHandlers.stopChecking)
	}
});