import '@servicenow/now-label-value';
import {t} from 'sn-translate';
import {CONSTANTS} from '../../constants';
import {
    getDisplayName,
    getDisplayValue,
    getValue
} from '../../fieldGetters';

const {EM_SEVERITY_TO_EDS_STATUS} = CONSTANTS.GENERAL;
//mapping for priority uses same colors as severity (part of workaround for tectonic 2.0.0+)
const EM_PRIORITY_TO_STATUS = EM_SEVERITY_TO_EDS_STATUS;

//DEF0046234
const DEFAULT_PRIORITY_LABEL = t('Low');
const defaultPriority = 4;

const formatComplexFieldToStringLabelValue = field => ({
    type: 'string',
    label: getDisplayName(field) || ' ',
    value: getDisplayValue(field) || ' '
});

const formatComplexFieldToHighlightedValue = (field, status, defaultLabel = ' ') => ({
    label: getDisplayName(field),
    value: {
        type: 'highlighted-value',
        label: getDisplayValue(field) || defaultLabel,
        status,
        showIcon: true
    }
});

const getLabelValueJsx = (items, noGap = false) => {
    const classList = {'now-p-inline-end--lg': !noGap};

    return (
        <now-label-value-stacked
            class={classList}
            truncated
            item-min-width='10px' //required to override default 50px that break our columns
            size='sm'
            items={items}/>
    );
};

export const getAlertCardContentJsx = item => {
    const {
        ciName,
        severity,
        priority,
        assignedTo,
        impactedServices,
        assignmentGroup
    } = item;

    const itemsCol1 = [
        formatComplexFieldToHighlightedValue(
            priority,
            EM_PRIORITY_TO_STATUS[getValue(priority)] || EM_PRIORITY_TO_STATUS[defaultPriority],
            DEFAULT_PRIORITY_LABEL
        ),

        formatComplexFieldToStringLabelValue(ciName)
    ];

    const itemsCol2 = [
        formatComplexFieldToHighlightedValue(
            severity,
            EM_SEVERITY_TO_EDS_STATUS[getValue(severity)]
        ),

        formatComplexFieldToStringLabelValue(assignedTo)
    ];

    const itemsCol3 = [
        formatComplexFieldToStringLabelValue(impactedServices),
        formatComplexFieldToStringLabelValue(assignmentGroup)
    ];

    return (
        <div className='columns-layout now-m-block--sm'>
            {getLabelValueJsx(itemsCol1)}
            {getLabelValueJsx(itemsCol2)}
            {getLabelValueJsx(itemsCol3, true)}
        </div>
    );
};