import {createRef} from '@servicenow/ui-renderer-snabbdom';
import {CONSTANTS} from  '../../constants';
import '@servicenow/now-icon';
import '@servicenow/now-button';
import {isEmpty as _isEmpty} from 'lodash';

const SEARCH_DESCRIPTION_ID = 'SEARCH_DESCRIPTION_ID';
const search = (inputRef, dispatch) => {
	let delayTimer;
	return function() {
		clearTimeout(delayTimer);
		delayTimer = setTimeout(function() {
			const {value} = inputRef.current;
			dispatch(CONSTANTS.ACTION_TYPES.SEARCH_TERM_CHANGED, {value: value.trim()});
		}, 1000);
	};
};

const clearSearch = (dispatch) => () => {
	dispatch(CONSTANTS.ACTION_TYPES.SEARCH_TERM_CHANGED, {value: ''});
};

export const view = (state, dispatch) => {
	const {searchTerm, searchDescription, searchLabel, clearSearchLabel} = state.properties;
	const configSearchAria = {'aria-label': clearSearchLabel};
	const inputRef = createRef();
	const clearSearchButton = !_isEmpty(searchTerm) ?
		<now-button-iconic
			variant='tertiary'
			className='clear-search'
			icon='close-outline'
			on-click={clearSearch(dispatch)}
			size='md'
			tooltip-content={clearSearchLabel}
			config-aria={configSearchAria}
			bare/> : null;
	return (
		<form role='search'
		      className='search'
		      attr-onsubmit="javascript:return false;">
			<label
				className='now-a11y-label'
				id={SEARCH_DESCRIPTION_ID}>
				{searchDescription}
			</label>
			<now-icon
				className='search-icon'
				icon='magnifying-glass-outline'
				size='md'/>
			<input
				ref={inputRef}
				className='search-input'
				aria-label={searchLabel}
				placeholder={searchLabel}
				aria-describedby={SEARCH_DESCRIPTION_ID}
				auto-focus
				type='text' //'search' - not supported in all browsers as we expect it
				value={searchTerm}
				on-input={search(inputRef, dispatch)}/>
			{clearSearchButton}
		</form>
	);
};
