import {t} from 'sn-translate';
import {isFunction as _isFunction, isString as _isString} from 'lodash';
import '@servicenow/now-label-value';
import '@servicenow/now-button';
import {CONSTANTS} from '../../constants';

const {RELATED_ITEMS_PAGE_SIZE} = CONSTANTS.DEFAULTS;
const partialToolTip = t('Showing partial list. Go to \'Alert list\' to view all');


export const getLabel = (nFrom, nTo, totalRecords) => {
	//In case the property to opt out the counter is active & there are no opened alerts
	//We need to  override and go strict to the 'No Alerts'
	const counter = ( nFrom > nTo) ? 0 : totalRecords;
	switch (counter) {
		case null:
		case undefined:
		case 0:
			return t('No records to display');
		case -1:
			return t('Showing {0} - {1}', nFrom, nTo);
		default:
			return t('Showing {0} - {1} out of {2}', nFrom, nTo, totalRecords);
	}
};

export const getFooterJsx = ({firstRecord, itemsSize, totalRecords, toolTipInfo, altFooterTextBuilder, selectedType}) => {
	const nFrom = firstRecord + 1;
	const nTo = firstRecord + itemsSize;

	const altLabel = _isFunction(altFooterTextBuilder) ? altFooterTextBuilder(selectedType) : null;
	const label = _isString(altLabel) ? altLabel : getLabel(nFrom, nTo, totalRecords);

	const labelItems = [
		{
			label,
			value: {
				type: 'string',
				value: ' '// space to ignore null
			}
		}
	];

	const infoIconJsx = (toolTipInfo && totalRecords
      && totalRecords > RELATED_ITEMS_PAGE_SIZE) ? <now-button-iconic
			className='now-m-inline-start--xs'
			icon='circle-info-outline'
			bare
			hide-padding
			tooltip-content={partialToolTip}/> :
		null;


	return (
		<div className='now-align block-end footer-container now-p-inline--lg'>
			<now-label-value-stacked
				className='now-align -inline-start now-p-block--xs'
				size='md'
				truncated
				items={labelItems}
				align='horizontal' />
			{infoIconJsx}

		</div>
	);
};