import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {forEach as _forEach, get as _get} from 'lodash';
import {CONSTANTS} from '../constants';
import {checkForError} from '../nocErrorHandler';
import {severityIcons} from '../severityIcons';
import {LOGGING} from '../utils';
import {generateSeverityColorsVars} from '../generateSeverityColorsCss';

const ACTION_TYPES = {
    COLORS_REQUEST: 'COLORS_REQUEST',
    COLORS_REQUEST_STARTED: 'COLORS_REQUEST_STARTED',
    COLORS_REQUEST_SUCCEEDED: 'COLORS_REQUEST_SUCCEEDED',
    COLORS_REQUEST_FAILED: CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED
};

const GQL_QUERY = `
query getColorsAndIcons($queryConditions: String!) {
  GlideRecord_Query {
    sa_contextual_colors_and_icons(queryConditions: $queryConditions) {
      _results {
        context_batch {
          value
        }
        context_id {
          value
        }
        default_context_color_code {
          value
        }
        default_context_icon {
          displayValue
        }
        accessible_context_color_code {
          value
        }
        accessible_context_icon {
          displayValue
        }
      }
    }
  }
}
`;

/**
 * Mapping from that sa_contextual_colors_and_icons has as 'context_id' into severity code.
 * sa_contextual_colors_and_icons don't store severity numeric code.
 * exported for unit testing
 */
export const SEVERITY_CODES_MAPPING = {
    clear_severity: 0,
    critical_severity: 1,
    major_severity: 2,
    minor_severity: 3,
    warning_severity: 4,
    information_severity: 5
};

const colorsFetchedHandler = createGraphQLEffect(GQL_QUERY, {
    startActionType: ACTION_TYPES.COLORS_REQUEST_STARTED,
    successActionType: ACTION_TYPES.COLORS_REQUEST_SUCCEEDED,
    errorActionType: ACTION_TYPES.COLORS_REQUEST_FAILED,
    variableList: ['queryConditions']
});

const REGEX_URI_WITH_PROTOCOL = /\^\w+\:\/\//;

const fixIconUrl = iconUrl => {
    if (!iconUrl || iconUrl.startsWith('/') || REGEX_URI_WITH_PROTOCOL.test(iconUrl))
        return iconUrl;

    return `/${iconUrl}`;
}

/**
 * Iterator function for result transform.
 * 'this' points to the output JS object: representing a map from severity code to its context.
 * @param rec - iterated record from sa_contextual_colors_and_icons
 * @private
 */
const transform_single = function(rec) {
    const {severityContext, isA11y} = this;
    const context = _get(rec, 'context_id.value', '');
    const severityCode = SEVERITY_CODES_MAPPING[context] || 0;

    const colorCode = _get(rec,
        isA11y ?
            'accessible_context_color_code.value' :
            'default_context_color_code.value',
        '');

    const iconUrl = fixIconUrl(_get(severityIcons, `[${severityCode}].url`, ''));

    severityContext[severityCode] = {
        colorCode,
        iconUrl
    };
};

//GraphQL read transform invoked manually from code
const transform = function (results, isA11y) {

    const severityContext = {};
    const it = transform_single.bind({severityContext, isA11y});
    _forEach(results, it);

    return severityContext;
};

const reduceColorsRecord = (styleContainer, item) => {
    const {
        context_batch: {value: field},
        context_id: {value: contextId},
        accessible_context_color_code: {value: colorCode},
        accessible_context_icon: {displayValue: iconUrl},
    } = item;

    if (!styleContainer[field])
        styleContainer[field] = {};

    const container = styleContainer[field];

    container[contextId] = {
        colorCode,
        iconUrl: fixIconUrl(iconUrl)
    };
    
    return styleContainer;
};

const handleColorsResponse = coeffects => {
    const {
		dispatch,
        updateState,
        properties,
		action: {payload, type}
    } = coeffects;

    LOGGING.log(`handleColorsResponse called by action ${type}`);

    const errors = checkForError({payload, dispatch, caller: 'handleColorsResponse'});
    if (errors)
        return;

    const {userMeta: {isA11y = true} = {}} = properties;

    const record = _get(payload, 'data.GlideRecord_Query.sa_contextual_colors_and_icons._results');

    //TODO: Refactor severity  vs. business_critically
    let colorsAndIcons = {};
    const isSeverity = _get(record[0], 'context_batch.value') === 'severity';
    if (isSeverity) {
        colorsAndIcons = transform(record, isA11y);

    } else {
        colorsAndIcons = record.reduce(reduceColorsRecord, {});
    }
    const severityVarsCss = generateSeverityColorsVars(colorsAndIcons);

    updateState({
        colorsAndIconsResource: {
            result: colorsAndIcons,
            severityVarsCss
        }
    });

    dispatch(() => ({type: CONSTANTS.ACTION_TYPES.NOC_COLORS_AND_ICONS_SET}));
    dispatch(() => ({type: CONSTANTS.ACTION_TYPES.NOC_COLORS_AND_ICONS_SET_PHASE_2}));
};

/**
 * Retrieve the arguments need to be send later on to the GQL
 * @param batchFields - Array of batchFields (e.g ['severity', 'business_criminality'])
 * @returns {{queryConditions: string}}
 */
const getVariables = (batchFields = ['severity']) => {
    const query = batchFields.reduce((buffer, field, index) => {
        const delimiter = (index === batchFields.length - 1) ? '' : ',';
        return buffer += `${field}${delimiter}`;
    }, '');

    return {
        queryConditions: `context_batchIN${query}^ORDERBYcontext_batch`
    };
};

const effectHandlers = {
    colorsFetchedHandler,
    handleColorsResponse
};

export default {
    ACTION_TYPES,
    effectHandlers,
    getVariables
};
