const ACTION_TYPES = {
		NOC_ACTION_CLICK: 'NOC_ACTION_CLICK'
};

const onActionClickEffect =  {
	effect(coeffects) {
		const {updateProperties, action: {payload}, dispatch} = coeffects;

		const isModalOpen = (payload.isModalOpen || (payload.action && payload.action.isModalOpen)) || false;

		updateProperties({isModalOpen});
	}
};

const effectHandlers = {
	onActionClickEffect
};

export default {
	ACTION_TYPES,
	effectHandlers
};
