import {createCustomElement} from '@servicenow/ui-core';
import {extendWithPerformance} from './extensions/performanceExtension';
import {extendWithDefaults} from './extensions/defaultsExtension';
import {isArray as _isArray, isFunction as _isFunction} from 'lodash';

const EXTENSIONS = {
	defaults: extendWithDefaults,
	performance: extendWithPerformance
};

const DEFAULT_EXTENSIONS = ['defaults'];

const asArray = (extensions) => {
	return extensions ? (_isArray(extensions) ? extensions : [extensions]) : [];
};

export const createNocElement = (name, baseConfig = {}, extensions = null) => {
	let mergedConfig = baseConfig;

	const extensionsArray = [DEFAULT_EXTENSIONS, ...asArray(extensions)];

	if (extensionsArray) {
		mergedConfig = extensionsArray.reduce((mergedConfig, extension) => {
			// either get functions by named map or as functions
			const extensionFunctions = _isFunction(extension) ? extension : EXTENSIONS[extension];
			const functions = asArray(extensionFunctions);

			return functions ?
				functions.reduce((mergedConfig, value) => value(name, mergedConfig), mergedConfig) :
				mergedConfig;

		}, baseConfig);
	}

	createCustomElement(name, mergedConfig);
};