/**
 * Getter functions that should be used to resolve fields that received from server side DTOs.
 * Must be user on population items (services).
 * May be used on ServicePreview fields (same format maintained).
 */
import {camelCase as _camelCase, isObject as _isObject, mapKeys as _mapKeys} from 'lodash';

export const isComplexField = field => _isObject(field);

const getSimpleField = field =>
	!isComplexField(field) ? field : null;

/**
 * Complex fields arrive with {value, display_value}.
 * To keep client side convention, transforming `display_value` to camelCase `displayValue`.
 * More precisely, any non-camel-case field inside complex field object will be changed to camelCase.
 * @param field
 * @returns {*} formattedField, or same field if non-complex field.
 */
export const formatComplexFieldsToCamelCase = field => {
    if (!isComplexField(field))
        return field;

    const formatted = _mapKeys(field, (v, k) => _camelCase(k));
    return formatted;
};

export const getValue = field => {
	if (!field) {
		return field;
	}

	return getSimpleField(field) || field.value;

};

export const getDisplayValue = field => {
	if (!field) {
		return field;
	}

	return getSimpleField(field) || field.displayValue || field.label || field.value || '';
};

export const getName = field => {
    if (!field) {
        return field;
    }

    return getSimpleField(field) || field.name;

};

export const getDisplayName = field => {
    if (!field) {
        return field;
    }

    return getSimpleField(field) || field.displayName || field.name || '';
};

export const getOrdinal = field => {
	if (!field || getSimpleField(field)) {
		return field;
	}

	if (field.type === 'number')
		return Number(field.value);

	return field.value;
};


export const getId = field => field.id || '';