import '@servicenow/now-card';
import {getDisplayName, getDisplayValue} from '../fieldGetters';

export const getCardFooterJsx = (item, footerFieldName) => {
    //if no footer field, then footer is omitted
    if (!footerFieldName) 
        return null;

    const footerField = item[footerFieldName];
    const displayName = getDisplayName(footerField);
    const displayValue = getDisplayValue(footerField);

    //if footer field value is not set, then footer is omitted
    if (!displayValue)
        return null;

    const label = `${displayName} : ${displayValue}`;

    return <now-card-footer label={label}/>;
};