import actionTypes from './actionTypes';

const ACTION_TYPES = {
	CARD_SELECTED: actionTypes.CARD_SELECTED
};

/**
 * Sets state for selected card id.
 * @param {*} coeffects 
 */
const cardSelected = coeffects => {
	const {
		updateProperties,
		dispatch,
		action: {
			payload
		},
		properties: {
			manageSelectedItemId
		}
	} = coeffects;

	const {
		id,
		isSelected,
		cardSelectedActionType = null
	} = payload;
	
	const selectedItemId = (isSelected && id) ? id : '';
	
	//update property only of not managed.
	if (!manageSelectedItemId)
		updateProperties({selectedItemId});
	
	//if select action name is provided, then dispatching it here with the original payload.
	if (cardSelectedActionType) {
		dispatch(() => ({
			type: cardSelectedActionType, 
			payload
		}));
	}

};

const effectHandlers = {
	cardSelected
};

export default {
	ACTION_TYPES,
	effectHandlers
};