import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {view} from './view';
import styles from './nocElementWithTooltip.scss';
import {createNocElement} from '../../createNocElement';

createNocElement('noc-element-with-tooltip', {
	renderer: {
		type: snabbdom,
		view
	},

	styles,

	properties: {
		tooltip: {
			default: '' //String
		},

		//type: must be DOM element
		targetElm: {
			default: null
		}
	}
});