import moment from 'moment';

/**
 * Translating UNIX timestamp into displayValue of DataTime, 
 * formatted with given format and aligned to given tz-offset.
 * @param {*} timestamp - Number, UNIX timestamp value.
 * @param {*} tz - timezone offset in ms, as given by `GlideDomain_Query.user.timeZoneOffset`
 * @param {*} dateTimeFormat - DateTime format, as given by `GlideDomain_Query.user.dateTimeFormat`
 */
export const displayTimestamp = (timestamp, tz, dateTimeFormat) => {
	//translate ms tz offset into standart timezone offset format `[+/-]HH:mm`
	const timezone = `${tz < 0 ? '-' : '+'}${moment(Math.abs(tz)).utc().format('HH:mm')}`;

	//fixing inconsistencies between Java DateTimeFormat and moment.js format
	const format = dateTimeFormat.replace('yyyy', 'YYYY').replace('dd', 'DD');

	//forcing timezone and formatting the time
	const displayValue = moment(timestamp || Date.now()).utcOffset(timezone).format(format);

	return displayValue;
};