import {LOGGING} from '../../utils';
import {actionTypes as seismicActionTypes} from '@servicenow/ui-core';

const {
	COMPONENT_CONNECTED,
	COMPONENT_DISCONNECTED,
} = seismicActionTypes;

const CHECK_INTERVAL_MS = 1000; //1 second

const ACTION_TYPES = {
	START_CHECKS: COMPONENT_CONNECTED,
	STOP_CHECKS: COMPONENT_DISCONNECTED,
	CHECK_IF_VISIBLE: 'SN_OPWS_CHECK_IS_VISIBLE#DO_CHECK',
	DEFAULT_ON_CHANGE: 'SN_OPWS_CHECK_IS_VISIBLE#CHANGED'
};

const triggerNextTick = dispatch => () => dispatch(ACTION_TYPES.CHECK_IF_VISIBLE, {});

const startChecking = ({dispatch}) => 
	triggerNextTick(dispatch)();

/**
 * Testing if given DOM element is visible on the screen.
 * https://stackoverflow.com/questions/19669786/check-if-element-is-visible-in-dom
 * @param {*} ref - ref object created by createRef()
 */
const isElementVisible = ref =>
	ref && ref.current && ref.current.offsetParent !== null;

const checkIfVisible = coeffects => {
	const {
		state: {
			isVisible
		},
		properties: {
			anchorElm,
			actionOnChange
		},
		updateState,
		dispatch
	} = coeffects;

	LOGGING.debug('effectIsVisible#checkIfVisible');

	if (isElementVisible(anchorElm) !== isVisible) {
		LOGGING.info(`isVisible was changed to ${!isVisible}. Dispatching ${actionOnChange}`, {caller: 'effectIsVisible.checkIfVisible'});
		//notify about change in visibility
		dispatch(actionOnChange, {
			isVisible: !isVisible
		});

		//keep state up-to-date
		updateState({
			isVisible: !isVisible
		});
	}

	//set next interval
	const visibilityInterval = setTimeout(triggerNextTick(dispatch), CHECK_INTERVAL_MS);

	updateState({visibilityInterval});
};

const stopChecking = coeffects => {
	const {
		visibilityInterval = null
	} = coeffects.state;

	LOGGING.info('effectIsVisible#stopChecking');
	clearTimeout(visibilityInterval);
};

const effectHandlers = {
	startChecking,
	checkIfVisible,
	stopChecking
};

export default {
	ACTION_TYPES,
	effectHandlers
};