import {LOGGING} from '../../utils';
import {debounce as _debounce} from 'lodash';

import {actionTypes} from '@servicenow/ui-core';
const {COMPONENT_BOOTSTRAPPED, COMPONENT_DISCONNECTED} = actionTypes;

const ACTION_TYPES = {
	OPWS_LOADER_BOOTSTRAP: COMPONENT_BOOTSTRAPPED,
	OPWS_LOADER_DISCONNECTED: COMPONENT_DISCONNECTED,
	OPWS_LOADER_SHOW: 'OPWS_LOADER_SHOW'
};

const opwsLoaderBootstrap = coeffects => {
	let {
		state,
		action,
		dispatch
	} = coeffects;

	LOGGING.debug('Bootstrapping loader', {caller: action.meta.id});

	const {properties: {debounceTime}} = state;

	const debounceFn = _debounce(() => {
		LOGGING.debug('Debounce time ended', {caller: action.meta.id});
		dispatch(ACTION_TYPES.OPWS_LOADER_SHOW, {});
	}, debounceTime);

	debounceFn();

	const {updateState} = coeffects;

	updateState({showing: false, debounceFn});
};

const _loaderSet = (coeffects, showing) => {
	let {
		state,
		action,
		updateState
	} = coeffects;

	LOGGING.debug(['Set loader showing=', showing], {caller: action.meta.id});

	const {debounceFn} = state;
	if (debounceFn) {
		LOGGING.debug('Cancel loader', {caller: action.meta.id});
		debounceFn.cancel();
	}

	updateState({showing, debounceFn: null});
};

const opwsLoaderCancel = coeffects => {
	_loaderSet(coeffects, false);
};

const opwsLoaderShow = coeffects => {
	_loaderSet(coeffects, true);
};

const effectHandlers = {
	opwsLoaderBootstrap,
	opwsLoaderCancel,
	opwsLoaderShow
};

export default {
	ACTION_TYPES,
	effectHandlers
};