import {get as _get} from 'lodash';
import {LOGGING} from '../../utils';
import {actionTypes as seismicActionTypes} from '@servicenow/ui-core';
import effectManageSelections from './effectManageSelections';

const {
	COMPONENT_CONNECTED
} = seismicActionTypes;

const MAX_RETRY_COUNT = 10;
const RETRY_DELAY = 500;

const ACTION_TYPES = {
	COMPONENT_CONNECTED,
	RETRY_ON_CONNECT: 'OPWS_RELATED_ITEMS_LIST#RETRY_ON_CONNECT'
};

const onConnect = coeffects => {
	const {
		dispatch,
		properties: {
			itemTypes,
			selectedSortOption
		},
		action: {
			payload: {
				retryCount = 0
			}
		},
		updateProperties,
		updateState
	} = coeffects;

	const selectedId = _get(itemTypes, '[0].id');

	if (selectedId) {
		LOGGING.info('snOpwsRelatedItemsList#onConnect: Triggering setSelectedType for first (default) option.');

		effectManageSelections.setSelectedType({
			selectedSortOption,
			itemTypes, 
			selectedId, 
			updateProperties, 
			updateState,
			dispatch
		});

	} else if (retryCount === MAX_RETRY_COUNT) {
		LOGGING.warn(`snOpwsRelatedItemsList#onConnect: itemTypes not ready. Giving up after ${MAX_RETRY_COUNT} re-tries!`);
	
	} else {
		LOGGING.info(`snOpwsRelatedItemsList#onConnect: itemTypes not ready. Will retry in ${RETRY_DELAY}ms...`);
		setTimeout(() => dispatch(ACTION_TYPES.RETRY_ON_CONNECT, {retryCount: retryCount + 1}), RETRY_DELAY);
	}
};

const effectHandlers = {
	onConnect
};

export default {
	ACTION_TYPES,
	effectHandlers
};