import {
	get as _get, 
	findKey as _findKey
} from 'lodash';

import {CONSTANTS} from '../../constants';
import {LOGGING} from '../../utils.js';

const ACTION_TYPES = {
	EDS_ALERT_ACTION_CLICKED: "NOW_ALERT#ACTION_CLICKED",
	NOC_CLEAR_NOTIFICATIONS: CONSTANTS.ACTION_TYPES.NOC_CLEAR_NOTIFICATIONS
};

const edsAlertActionClicked = coeffects => {
	const {
		action, 
		dispatch
	} = coeffects;

	const {action: notificationAction} = action.payload;
	const isDismiss = notificationAction && notificationAction.type === 'dismiss';

	if (isDismiss)
		dispatch(ACTION_TYPES.NOC_CLEAR_NOTIFICATIONS, {notificationAction});
};

const clearNotificationEffect = coeffects => {
	const {
		action,
		properties,
		updateProperties
	} = coeffects;

	const {
		notifications = []
	} = properties;
	
	const notificationId = _get(action, 'payload.notificationAction.id', '');

	if (!notificationId) {
		//clear all notifications, if no specific id provided
		updateProperties({
			notifications: []
		});
		return;
	}

	const idx = _findKey(notifications, n => n.action.id === notificationId);

	if (!idx) {
		LOGGING.info(`clearNotificationEffect: notification not found by id ${notificationId}. Doing nothing.`);
		return;
	}

	const newNotifications = [...notifications];
	newNotifications.splice(idx, 1);

	updateProperties({
		notifications: newNotifications
	});
};

const effectHandlers = {
	edsAlertActionClicked,
	clearNotificationEffect
};

export default {
	ACTION_TYPES,
	effectHandlers
};