import {cloneDeep as _cloneDeep} from 'lodash';

const DEFAULT_PREFIX_PERFORMANCE = 'PERFORMANCE';
const DEFAULT_SUFFIX_PERFORMANCE = '';

/**
 *
 * @param duration
 * @returns {string}
 * @private
 */
const DEFAULT_LOG_FORMATTER = (duration) => `Call took ${duration} milliseconds.`;
const DEFAULT_ALLOW_PERFORMANCE = true;
const DEFAULT_LOG_LEVEL = 'info';

const defaultOptions = {
	allow: DEFAULT_ALLOW_PERFORMANCE,
	prefix: DEFAULT_PREFIX_PERFORMANCE,
	suffix: DEFAULT_SUFFIX_PERFORMANCE,
	formatter: DEFAULT_LOG_FORMATTER,
	logLevel: DEFAULT_LOG_LEVEL
};

/**
 *
 * @param options
 * @constructor
 */
export function Performance(options = {}) {
	try {
		/**
		 * @param options
		 * @returns {*}
		 */
		this.getAllow = (options = this.options) => options.allow;

		/**
		 *
		 * @param allow
		 */
		this.setAllow = (allow) => {
			this.options = {...this.options, allow};
		};

		/**
		 * Reset 'allow' to the the Browser window state (use default)
		 */
		this.resetAllow = () => {
			this.setAllow(DEFAULT_ALLOW_PERFORMANCE);
		};

		/**
		 * @param options
		 * @returns {*}
		 */
		this.getLogLevel = (options = this.options) => options.logLevel;

		/**
		 *
		 * @param logLevel
		 */
		this.setLogLevel = (logLevel) => {
			this.options = {...this.options, logLevel};
		};

		/**
		 *
		 */
		this.resetLogLevel = () => {
			this.setLogLevel(DEFAULT_LOG_LEVEL);
		};

		/**
		 *
		 * @param logCallback
		 */
		this.setLogCallback = (logCallback) => {
			this.options = {...this.options, logCallback};
		};

		/**
		 * Default function to log the measure
		 *
		 * @param duration
		 * @param fn
		 * @param options
		 * @private
		 */
		this._log = (duration, fn, options = {}) => {
			const {logCallback, prefix, suffix, formatter, logLevel} = options;
			logCallback ? logCallback(formatter(duration), logLevel, {
				suffix: prefix + (suffix ? '.' + suffix : ''),
				caller: fn
			}) : null;
		};

		/**
		 * Clear existing measures
		 *
		 * @param fnName
		 */
		this.clearMeasures = (fnName) => {
			performance.clearMeasures(fnName);
		};

		/**
		 * Get the current existing measures
		 *
		 * @param fnName
		 * @returns {PerformanceEntryList}
		 */
		this.getMeasures = (fnName) => performance.getEntriesByName(fnName);


		/**
		 * MeasureCall is used to measure the execution time taken by the function.
		 * Arguments used by the function should be passed in an array
		 *
		 * @param fn
		 * @param params [*]
		 * @param clearMeasures
		 * @param options
		 * @returns {*}
		 */
		this.measureCall = function(fn, [...params], clearMeasures = true, options = {}) {
			if (!this.getAllow()) {
				return fn(...params);
			}

			performance.mark(`${fn.name}_START`);
			try {
				return fn(...params);
			} finally {
				performance.mark(`${fn.name}_END`);
				performance.measure(fn.name, `${fn.name}_START`, `${fn.name}_END`);

				const measures = this.getMeasures(fn.name);
				const measure = measures ? measures.reduce((result, measure) => result + measure.duration, 0) : 0;

				this._log(measure, fn, {...this.options, ...options});

				if (clearMeasures) {
					this.clearMeasures(fn.name);
				}
			}
		};
	} finally {
		this.options = {..._cloneDeep(defaultOptions), ...options};
	}
}