import cuid from 'cuid';
import {CONSTANTS} from '../../constants';

const ACTION_TYPES = {
	NOC_SHOW_NOTIFICATION: CONSTANTS.ACTION_TYPES.NOC_SHOW_NOTIFICATION
};

/**
 * This effects expect payload to be a notification.
 * It will add the notification on the state.
 */
const showNotificationEffect = coeffects => {
	const {
		properties,
		updateProperties,
		action: {payload: toShow}
	} = coeffects;
	
	if (!toShow || !toShow.content)
		return;

	const {
		notifications = []
	} = properties;

	const {
		status = 'info',
		icon = 'circle-info-outline',
		content = '',
		header = '',
		type = 'dismiss',
		action: notificationAction = {}
	} = toShow;

	//intenal unique id per notification to find it when closing
	const id = cuid();

	const newNotification = {
		id,
		status,
		icon,
		content,
		header,
		action: {
			type,
			...notificationAction, //custom action object can override type
			id
		}
	};

	updateProperties({
		notifications: [
			...notifications,
			newNotification
		]
	});
};

const effectHandlers = {
	showNotificationEffect
};

export default {
	ACTION_TYPES,
	effectHandlers
};