import {t} from 'sn-translate';
import {get as _get} from 'lodash';
import {getDisplayValue} from '../fieldGetters';
import '@servicenow/now-card';
import '@servicenow/now-highlighted-value';
const LABEL_OPEN_RECORD_FORM = t('Open record form');

export const getCardHeaderJsx = ({item, openFormActionType, descriptionFieldName, typeId}) => {
    const {
        id,
        sysId,
        serviceId,
        number,
        metadata
    } = item;

    const tagline = number ?
        {
            label: getDisplayValue(number),
            variant: 'secondary'
        } :
        null;

    const description = _get(item, descriptionFieldName, '');
    const heading = {
        label: getDisplayValue(description) || '',
        lines: 2
    };

    const actions = openFormActionType !== false ?
        [
            {
                icon: 'open-link-outline',
                label: LABEL_OPEN_RECORD_FORM,
                disabled: false,
                ...(openFormActionType ? {clickActionType: openFormActionType} : {})
            }
        ] :
        null;

    const actionsPayload = {
        serviceId,
        alertId: sysId,
        id,
        typeId
    };

    const componentProperties = {
        appendToPayload: actionsPayload,
        ...(heading && heading.label ? {heading} : {}),

        //if `openFormActionType === false`, then 'open form' action icon will not be added.
        ...(actions ? {actions} : {}),

        //if tagline is not defined (when no `number` field on item), 
        //then don't pass it as proeprty to `now-card-header`
        ...(tagline && tagline.label ? {tagline} : {})
    };

    return <now-card-header {...componentProperties}>
        { metadata && <now-highlighted-value slot="metadata"
                label={metadata.label}
                icon={metadata.icon}
                status={metadata.status}/> }
    </now-card-header>;
};