import {createNocElement} from '../../createNocElement';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {view} from './snNocNotificationsView';
import styles from './snNocNotifications.scss';

/**
 * Seismic component `sn-noc-notifications`.
 * Visual part of notifications tray.
 * Wraps around `now-alert-list`.
 */
createNocElement('sn-noc-notifications', {
	renderer: {
		type: snabbdom,
		view
	},

	styles,

	properties: {
		notifications: {
			default: []
		}
	}
});