import {t} from 'sn-translate';
import '@servicenow/now-button';

const LABEL_BACK = t('Back');

const notifyBackAction = (dispatch, actionType) => 
	actionType ? 
		() => dispatch(actionType, {}) :
		() => {};


export const getBackButtonJsx = ({dispatch, showBackButton, actionType}) => 
	showBackButton ? (
		<now-button-iconic 
			className='now-align -inline-start opws-list-back-btn'
			icon='chevron-left-outline'
			bare
			hide-padding
			aria-label={LABEL_BACK}
			size='md'
			variant='tertiary'
			tooltip-content={LABEL_BACK}
			on-click={notifyBackAction(dispatch, actionType)}
		/>
	) : null;