const ACTION_TYPES = {
	EDS_ITEMS_SET: "NOW_ALERT_LIST#ITEMS_SET"
};

/**
 * NDS alert list is set to manage array of items from outside.
 * This effect should listen for ITEMS_SET action from the alerts lit and updaet the notifications array on state.
 */
const updateNotificationsEffect = coeffects => {
	const {
		action,
		updateProperties
	} = coeffects;

	const {value: notifications} = action.payload;

	updateProperties({
		notifications
	});
};

const effectHandlers = {
	updateNotificationsEffect
};

export default {
	ACTION_TYPES,
	effectHandlers
}