import '@servicenow/now-heading';
import '@servicenow/now-button';
import '../sn-opws-time-ago';
import {t} from 'sn-translate';
import actionTypes from './actionTypes';
import effectRefreshRequested from './effectRefreshRequested';
import {getBackButtonJsx} from './getBackButtonJsx';

const LABEL_REFRESH = t('Refresh list');
const TIME_AGO_FORMAT = 'Last updated {0}';
const __TIME_AGO_FORMAT_FOR_TRANSLATOR = t('Last updated {0}');

const requestRefresh = dispatch => () => 
	dispatch(effectRefreshRequested.ACTION_TYPES.REFRESH_REQUESTED_INTERNAL, {forceRefresh: true});

export const getHeaderJsx = args => {
	const {
		headerTitle,
		lastUpdated,
		dispatch,
		showBackButton, 
		hideRefreshButton
	} = args;

	const heading = headerTitle ? (
		<now-heading 
			className='now-align -inline-start opws-list-heading'
			label={headerTitle}
			variant='header-secondary' 
			has-no-margin 
			level='2'
		/>) : 
		null;

	const refreshButton = hideRefreshButton ? null : (
		<now-button-iconic 
			className='now-align -inline-end opws-list-refresh-btn'
			icon='sync-outline'
			bare
			size='md'
			variant='tertiary'
			tooltip-content={LABEL_REFRESH}
			on-click={requestRefresh(dispatch)}
		/>
	);

	const timeAgoJsx = hideRefreshButton ? null : (
		<div className='time-ago'>
			<sn-opws-time-ago
				time-ago-format={TIME_AGO_FORMAT} 
				last-updated={lastUpdated}
			/>
		</div>
	);

	const backButton = getBackButtonJsx({dispatch, showBackButton, actionType: actionTypes.BACK_ACTION});

	return (
		<div className='now-align -block-start horizontal opws-list-header-container'>
			<div className='now-align -block-start space-between now-m-block-end--xl'>
				{backButton}
				{heading}
				{refreshButton}
			</div>
			<div className='now-align flex-full-width'>
				<slot name='header-extra-content' className='now-align flex-full-width'/>
			</div>

			{timeAgoJsx}
		</div>
	);
};