import {createNocElement} from '../../createNocElement';
import {createActionConfig} from '../../createActionConfig';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {view} from './snOpwsTimeAgoView';
import styles from './snOpwsTimeAgo.scss';
import {actionTypes} from '@servicenow/ui-core';
import snOpwsTimeAgoEffect from './snOpwsTimeAgoEffect';

const {
	COMPONENT_CONNECTED,
	COMPONENT_DISCONNECTED
} = actionTypes;

createNocElement('sn-opws-time-ago', {
	renderer: {
		type: snabbdom,
		view
	},

	styles,

	initialState: {
		tick: Date.now()
	},

	properties: {
		interval: {
			default: 1000
		},
		lastUpdated: {
			default: null
		},

		timeAgoFormat: {
			default: ''
		},

		textItems: {
			default: []
		}
	},

	actionHandlers: {
		[COMPONENT_CONNECTED]:
			createActionConfig(snOpwsTimeAgoEffect.effectHandlers.registerTimerAction),
		[COMPONENT_DISCONNECTED]:
			createActionConfig(snOpwsTimeAgoEffect.effectHandlers.unregisterTimerAction)
	}
});