import {snabbdom} from '@servicenow/ui-renderer-snabbdom';
import {createNocElement} from '../createNocElement';
import {getCardHeaderJsx} from './getCardHeaderJsx';
import {getCardFooterJsx} from './getCardFooterJsx';
import '@servicenow/now-card';
import {isFunction as _isFunction, map as _map} from 'lodash';
import stylesBase from './cardComponentCreator.scss';
import {getDisplayName, getDisplayValue, getValue} from '../fieldGetters';
import {CONSTANTS} from '../constants';
import {t} from 'sn-translate';

const {EM_SEVERITY_TO_EDS_STATUS} = CONSTANTS.GENERAL;
const EM_PRIORITY_TO_STATUS = EM_SEVERITY_TO_EDS_STATUS;
const DEFAULT_PRIORITY_LABEL = t('Low');
const defaultPriority = 4;

/**
 * Generates Seismic component for card on items-list.
 * `componentName` - new component with this name will be registered.
 * `view` - function for view()
 * `styles` - style imports.
 */
export const createCardComponent = ({
	componentName,
	view,
	styles = ''
}) => createNocElement(componentName, {
  renderer: {
    type: snabbdom,
    view
  },

  styles: [stylesBase, styles].join('\n\n'),

  properties: {
    isSelected: {
      default: false
    },

    selectedType: {
      default: null
    },

    item: {
      default: null
    }
  }
});

/**
 * Generates view() function for the card component.
 * `getCardContentJsx` - function that gets item as input and returns JSX part for the card content.
 * `openFormActionType` - String, optional. Override default action type `NOW_CARD#SELECTED_SET` when now-card is selected 
 * `cardSelectedActionType` - Action type for card selection.
 * `footerFieldName` - complex field to be displayed on card footer. Omit this to disable card footer.
 */
export const generateViewFn = ({
    getCardContentJsx = () => null,
    openFormActionType = null,
    cardSelectedActionType = null,
    footerFieldName = null,
    descriptionFieldName = 'description',
    size = 'md'
}) => {

    return state => {
        const {
            item,
            selectedType: {
                id: typeId
            },
            isSelected
        } = state.properties;

        const {id} = item;

        const cardClickPayload = {
            typeId,
            isSelected: !isSelected,
            id,
            item,
            cardSelectedActionType
        };

        const headerJsx = getCardHeaderJsx({item, openFormActionType, descriptionFieldName, typeId});
        const content = getCardContentJsx(item, typeId);
        const footer = getCardFooterJsx(item, footerFieldName);

        return (
            <now-card
                interaction='select'
                append-to-payload={cardClickPayload}
                selected={isSelected}
                manage-selected
                size={size}>
                {headerJsx}
                {content}
                {footer}
            </now-card>
        );
    };
};

export const formatComplexFieldToStringLabelValue = field => ({
  label: getDisplayName(field) || ' ',
  value: {
    type: 'string',
    value: getDisplayValue(field) || ' '
  }
});

export const formatComplexFieldToHighlightedValue = (field, status, defaultLabel = ' ') => ({
  label: getDisplayName(field) || ' ',
  value: {
    type: 'highlighted-value',
    label: getDisplayValue(field) || defaultLabel,
    status,
    showIcon: true
  }
});

export const getLabelValueJsx = (items, noGap = false) => {
  const classList = {'now-p-inline-end--lg': !noGap};
  return (
    <now-label-value-stacked
      class={classList}
      truncated
      item-min-width='10px'
      size='sm'
      items={items}/>
  );
};


export const getAvatarElementJsx = field => {
  return  (
      <span className='card-layout-col-width avatar'>
       <now-avatar size='sm' className='now-m-inline-end--sm' user-name={getDisplayValue(field)}>
        </now-avatar>
        <now-label-value-stacked
          className=''
          truncated
          item-min-width='10px' // Overriding default 50px of item-min-width
          // to make sure that label-value doesn't overflow within card column
          size='sm'
          items={[formatComplexFieldToStringLabelValue(field)]}/>
        </span>
  );
};

export const getLabelValueElementJsx = field => {
  return (
   <span className='card-layout-col-width'>
      <now-label-value-stacked
        className=''
        truncated
        item-min-width='10px' // Overriding default 50px of item-min-width
        // to make sure that label-value doesn't overflow within card column
        size='sm'
        items={[formatComplexFieldToStringLabelValue(field)]}/>
    </span>
  );
};


export const getHighlightedLabelValueElementJsx = field => {
    return (
      <span className='card-layout-col-width'>
        <now-label-value-stacked
          className=''
          truncated
          item-min-width='10px' // Overriding default 50px of item-min-width
          // to make sure that label-value doesn't overflow within card column
          size='sm'
          items={[formatComplexFieldToHighlightedValue(
            field,
            EM_PRIORITY_TO_STATUS[getValue(field)] || EM_PRIORITY_TO_STATUS[defaultPriority],
            DEFAULT_PRIORITY_LABEL
          )]}/>
      </span>
    );
};

const getCardJsx = (nocCardAvatar) => {
  return (
    <div className='noc-card -avatar now-align'>
      {nocCardAvatar}
    </div>
      );
};

const getindicatorCard = item => {
  const {jsxBuilder, field} = item;
  if (!_isFunction(jsxBuilder))
    return null;

  return jsxBuilder(field);
};

export const getIndicatorCardWithAvatarJsx = items => {
  const nocCardAvatar = _map(items, getindicatorCard).filter(Boolean);
  return getCardJsx(nocCardAvatar);
};