import actionTypes from './actionTypes';

const ACTION_TYPES = {
	REFRESH_REQUESTED_INTERNAL: 'OPWS_RELATED_ITEMS_LIST#REFRESH_REQUESTED_INTERNAL'
};

/**
 * Dispatches public `REFRESH_REQUESTED` action with all the required info in payload.
 * @param {*} coeffects 
 */
const completeRefreshAction = coeffects => {
	const {
		dispatch,
		action: {
			payload
		},
		state: {
			firstRecord,
			selectedSortOption
		},
		properties: {
			selectedType
		}
	} = coeffects;

	const fullPayload = {
		selectedType,
		selectedSortOption,
		firstRecord,
		...payload
	};

	dispatch(actionTypes.REFRESH_REQUESTED, fullPayload);
};

const effectHandlers = {
	completeRefreshAction
};

export default {
	ACTION_TYPES,
	effectHandlers
};