import {mapValues as _mapValues} from 'lodash';
import {t} from 'sn-translate';
import {formatComplexFieldsToCamelCase} from '../../fieldGetters';
import {LOGGING} from "../../utils";
import {deepFreeze} from "../../deepFreeze";

const LABEL_LAST_REMOTE_TIME = t('Last event');
const LABEL_READ_ONLY_ALERT = t('Read only');

const READ_ONLY_METADATA = deepFreeze({
	label: LABEL_READ_ONLY_ALERT,
	icon: '',
	status: 'low'
});

const formatItem = ({serviceId}, item) => {
    const transformedItem = {
		..._mapValues(item, formatComplexFieldsToCamelCase),
		id: item.sysId,
		...(serviceId ? {serviceId} : {}),

		lastRemoteTime: {
			displayName: LABEL_LAST_REMOTE_TIME,
        	displayValue: item.lastRemoteTime
		}
	};
    if (item.readOnly) transformedItem.metadata = READ_ONLY_METADATA;

    return transformedItem;
};

export const formatAlertsResponse = (alerts = [], serviceId = null) => {
	LOGGING.debug('Formatted Alerts Response was called', {extra: alerts});
    const formattedAlerts = alerts.map(formatItem.bind(null, {serviceId}));
    return formattedAlerts;
};