import snabbdom from '@servicenow/ui-renderer-snabbdom';

const {createElement} = snabbdom;

export const getCardJsx = (args, item) => {
	const {
		selectedItemId,
		selectedType
	} = args;

	const {cardComponentName} = selectedType;

	if (!cardComponentName || !item || !item.id)
		return null;

	const isSelected = (selectedItemId === item.id);
	const props = {
		key: item.id,
		item,
		selectedType,
		isSelected,
		//generic gap between cards in the card list 
		//attribute 'class' is set by snabbdom and interpreted as CSS class list by the DOM.
		//try not to copy-paste this please, there may be better solutions for your case.
		'attr-class': 'now-m-block-end--lg' 
	};

	return createElement(cardComponentName, props);
};