import '@servicenow/now-dropdown';
import {t} from 'sn-translate';
import {get as _get} from 'lodash';
import effectManageSelections from './effectManageSelections';

const LABEL_CHOOSE_TYPE = t('View');
const LABEL_SORT_BY = t('Sort by');

const getLabelWithDropdown = ({dropdownId, items, selected, label, isLast = false, isSingle = false}) => {
	const labelId = `${dropdownId}_label`;
	const cssClass = {
		'now-align': true,
		'now-m-block-end--lg': true,
		'horizontal': true, 
		'control': true,
		'is-single': isSingle,
		'now-m-inline-end--lg': !isLast
	};

	return (
		<div class={cssClass}>
			<label id={labelId} className='dropdown-label now-align -block-start'>
				{label}
			</label>

			<now-dropdown 
				id={dropdownId}
				className='now-align -block-end now-m-block-start--xs'
				aria-labelledby={labelId}
				append-to-payload={{dropdownId}}
				size='sm'
				variant='tertiary'
				manage-selected-items
				selected-items={[selected]}
				items={items}/>
		</div>
	);
};

export const getChoiceControlsJsx = ({itemTypes, selectedType, selectedSortOption}) => {
	const {
		sortOptions = [],
		id: selectedTypeId = ''
	} = selectedType;

	const sortOptionId = selectedSortOption || _get(sortOptions, '[0].id', '');

	const typesControlJsx = (itemTypes && itemTypes.length > 1) ? 
		getLabelWithDropdown({
			dropdownId: effectManageSelections.DROPDOWN_IDS.DROPDOWN_ITEM_TYPE, 
			label: LABEL_CHOOSE_TYPE,
			items: itemTypes, 
			selected: selectedTypeId
		}) :
		null;

	const sortOptionsControl = getLabelWithDropdown({
		dropdownId: effectManageSelections.DROPDOWN_IDS.DROPDOWN_CARDS_SORT_BY, 
		label: LABEL_SORT_BY,
		items: sortOptions, 
		selected: sortOptionId,
		isLast: true,
		isSingle: (typesControlJsx === null)
	});

	return (
		<div className='now-align -block-end'>
			{typesControlJsx}
			{sortOptionsControl}
		</div>
	);
};