import {isArray as _isArray, isFunction as _isFunction} from 'lodash';

import {LOGGING} from './utils';

export const createActionConfig = (handler, isPrivate = true) => {
	if (!handler) {
		LOGGING.warn(new Error('createActionConfig skipping because handler is not defined!'));
		return {};
	}

	if (_isFunction(handler)) {
		handler = {
			effect: handler
		};

	} else if (_isArray(handler)) {
		throw new Error('Array of handlers not supported. Only single hander is supported');
	}

	return {
		...handler,
		stopPropagation: (isPrivate === true)
	};
};