import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-tooltip';

const onSlotInsert = updateProperties => ({elm: slot}) => {
	const targetElm = (slot.assignedNodes() || []) [0];
	
	if (targetElm)
		updateProperties({targetElm});
};

export const view = (state, {updateProperties}) => {
	const {
		targetElm, 
		tooltip
	} = state.properties;

	if (!tooltip)
		return <slot/>;

	return (
        <Fragment>
			<slot 
				// Not using on-slotchange, it doesn't work in Safari. `hook-insert` works everywhere (including IE11)
				hook-insert={onSlotInsert(updateProperties)}/>
			
			<now-tooltip
				target-ref={targetElm} 
				content={tooltip}/>
		</Fragment>
    );
};