/**
 * Custom effect that delegates on seismic updateStateEffect, and also provides a way to dispatch any action(s)
 * after the state was updated.
 */
import {identity as _identity, isArray as _isArray, isFunction as _isFunction} from 'lodash';

import {createUpdateStateEffect} from '@servicenow/ui-effect-update-state';
import {LOGGING} from './utils.js';

const createPromise = (debugKey) => {
	const resolver = {
		resolve: null
	};

	const promise = new Promise(resolve => {
		LOGGING.debug(`nocUpdateStateEffect ${debugKey}: promise init`);
		resolver.resolve = resolve;
	});

	return {
		promise,
		resolver
	};
};

const createInterceptors = (reducerFn, debugKey) => {
	return ({coeffects, effects}) => {
		const {type} = coeffects.action;
		LOGGING.debug(`nocUpdateStateEffect [${type}] ${debugKey}: interceptor`);
		
		const {promise, resolver} = createPromise(debugKey);

		const initPromise = () => promise;

		const originalReducerFn = (state, action) => reducerFn(state, action, initPromise);
		const seismicUpdateStateEffect = createUpdateStateEffect(originalReducerFn);

		if (seismicUpdateStateEffect && _isArray(seismicUpdateStateEffect.args))
			seismicUpdateStateEffect.args.push(coeffects);

		else
			throw new Error('createUpdateStateEffect expected to return object handler with args array inside.');
		

		//creating seismic effect in runtime
		const afterStateUpdatedEffect = coeffects => {
			const {dispatch} = coeffects;
			//using timeout to avoid stack overflows. Not seen them happen, just suspecting there might be such.
			setTimeout(() => {
				LOGGING.debug(`nocUpdateStateEffect ${debugKey}: afterStateUpdatedEffect`);
				if (_isFunction(resolver.resolve)) {
					LOGGING.debug(`nocUpdateStateEffect ${debugKey}: promise resolve`);
					resolver.resolve(dispatch);
				}
			}, 1);
		};

		//using 'effects' array to invoke dynamic effects.
		return {
			effects: [
				...effects,
				seismicUpdateStateEffect,
				{
					effect: afterStateUpdatedEffect,
					args: [coeffects]
				}
			],
			coeffects
		};
	};
};

/**
 * Creates custom updateStateEffect handler, which allows dispatching an Action after state was updated.
 * @param reducerFn {Function} will be called with arguments (state, action, getAfterUpdatePromise).
 * reducerFn expected to return new immutable state.
 * reducerFn may call function getAfterUpdatePromise and use the returned Promise:
 *  afterUpdatePromise.then(dispatch => {...});
 *
 * `then` callback will be called asynchronously after state was updated, and will receive dispatch function.
 * dispatch function may be used to dispatch any Action.
 *
 * @returns {{effect: nocUpdateStateEffect, args: *[]}} effectHandler
 */
export const createNocUpdateStateEffect = reducerFn => {
	const debugKey = `i:${Math.round(Math.random() * 1000000 + 1)}`;

	return {
		effect: _identity, 

		/* FIXME: https://code.devsnc.com/dev/sn-seismic/issues/275 
		* Don't try to invoke NocUpdateStateEffect from after interceptor of another effect.
		* Follow up on the issue from above.
		*/
		interceptors: [
			createInterceptors(reducerFn, debugKey)
		]
	};
};
