import {get as _get, isString as _isString} from 'lodash';
import {LOGGING} from '../../utils';
import effectRefreshRequested from './effectRefreshRequested';
import actionTypes from './actionTypes';
import {DROPDOWN_IDS} from './itemTypes';

const ACTION_TYPES = {
	ITEMS_SET: 'NOW_DROPDOWN#SELECTED_ITEMS_SET'
};

const setSelectedType = args => {
	const {
		selectedSortOption: currentSortOption,
		itemTypes, 
		selectedId, 
		updateProperties, 
		updateState,
		dispatch,
	} = args;

	const selectedType = itemTypes.find(it => it.id === selectedId);

	if (!selectedType) {
		LOGGING.warn(`Could not find selectedId '${selectedId}' in array of selectedTypes`, {
			output: {objects: true}, 
			extra: {selectedType}
		});

		return;
	}

	// selected from properties or the first in list if not available
	const selectedSortOption = currentSortOption || _get(selectedType, 'sortOptions[0].id', '');

	updateProperties({selectedType, items: []}); 
	//reset selectedSortOption when type changes
	updateState({selectedSortOption});

	//reset selected card state for cases when manageSelectedItemId=false.
	dispatch(actionTypes.CARD_SELECTED, {});

	//trigger data fetch from server
	dispatch(effectRefreshRequested.ACTION_TYPES.REFRESH_REQUESTED_INTERNAL, {
		selectedType,
		selectedSortOption,
		forceRefresh: true
	});
};


const setSortBy = args => {
	const {
		updateState, 
		selectedId: selectedSortOption,
		dispatch
	} = args;

	updateState({selectedSortOption});
	dispatch(effectRefreshRequested.ACTION_TYPES.REFRESH_REQUESTED_INTERNAL, {selectedSortOption});
	dispatch(actionTypes.SORT_SELECTED, {selectedSortOption});
};

const effectManageSelections = coeffects => {
	const {
		action: {
			payload: {
				dropdownId,
				value = []
			}
		},
		properties: {
			itemTypes
		},
		updateProperties,
		updateState,
		dispatch
	} = coeffects;

	const selectedId = value[0];
	
	if (!selectedId || !_isString(selectedId)) {
		LOGGING.warn(`Unexpected value in NOW_DROPDOWN#SELECTED_ITEMS_SET, expected array of 1 item which is id, got ${selectedId}`, {
			output: {objects: true}, 
			extra: {dropdownId}
		});
		return;
	}
	
	switch (dropdownId) {
		case DROPDOWN_IDS.DROPDOWN_ITEM_TYPE:
			setSelectedType({
				itemTypes, 
				selectedId, 
				updateProperties, 
				updateState, 
				dispatch
			});
			break;

		case DROPDOWN_IDS.DROPDOWN_CARDS_SORT_BY:
			setSortBy({
				updateState, 
				selectedId,
				dispatch
			});
			break;
	}
};

const effectHandlers = {
	effectManageSelections
};

export default {
	DROPDOWN_IDS,
	ACTION_TYPES,
	effectHandlers,
	setSelectedType
};