/**
 * TODO Requested: a single function for turning on all options (objects, tables, etc.)
 * TODO Requested: By default, print the caller function name in logging same as Performance is doing
 * TODO Requested: When printing Performance logs, use keys/tags to group same
 */

import {
	cloneDeep as _cloneDeep,
	get as _get,
	isArray as _isArray,
	isEmpty as _isEmpty,
	isFunction as _isFunction,
	isObject as _isObject,
	mapValues as _mapValues
} from 'lodash';

const DEFAULT_PREFIX = 'NOC';
const HIDDEN_OBJECT_DATA = '{Hidden Object Data}';

const LOG_LEVEL_TRACE = 'trace';
const LOG_LEVEL_DEBUG = 'debug';
const LOG_LEVEL_LOG = 'log';
const LOG_LEVEL_INFO = 'info';
const LOG_LEVEL_WARN = 'warn';
const LOG_LEVEL_ERROR = 'error';

export const LogLevel = Object.freeze({
	TRACE: {name: LOG_LEVEL_TRACE, order: 100},
	DEBUG: {name: LOG_LEVEL_DEBUG, order: 200},
	LOG: {name: LOG_LEVEL_LOG, order: 300},
	INFO: {name: LOG_LEVEL_INFO, order: 400},
	WARN: {name: LOG_LEVEL_WARN, order: 500},
	ERROR: {name: LOG_LEVEL_ERROR, order: 600},

	parse: (logLevel) => {
		return logLevel ? (typeof logLevel === 'object') ? logLevel : LogLevel[logLevel.toUpperCase()] : null;
	},

	order: (logLevel) => {
		logLevel = parse(logLevel);
		return logLevel ? logLevel.order : -1;
	}
});


const TARGET_CONSOLE = 'console';
const TARGET_SERVER = 'server';
const TARGET_NOTIFICATION = 'notification';
export const Target = Object.freeze({
	CONSOLE: TARGET_CONSOLE,
	SERVER: TARGET_SERVER,
	NOTIFICATION: TARGET_NOTIFICATION,

	parse: (target) => {
		return target ? (typeof target === 'object') ? target : Target[target.toUpperCase()] : null;
	}
});

/**
 * TODO
 *
 * @param msg
 * @param prefix
 * @param caller
 * @returns {string}
 * @private
 */
const _SERVER_FORMATTER = function(msg, {suffix, caller}) {
};

/**
 *
 * @param msg
 * @param prefix
 * @param caller
 * @returns {string}
 * @private
 */
const _NOTIFICATION_FORMATTER = function(msg, {suffix, caller}) {
	return (caller ? (_isFunction(caller) ? caller.name : caller) + ': ' : '') + msg;
};

/**
 *
 * @param msg
 * @param prefix
 * @param caller
 * @returns {string}
 * @private
 */
const _CONSOLE_FORMATTER = function(msg, {prefix, suffix, caller}) {
	const callerName = caller ? (_isFunction(caller) ? caller.name : caller) : '';

	if (_isArray(msg)) {
		msg = msg.join('');
	}

	const parts = [prefix, suffix, callerName];

	const text = '[' + parts.reduce((previous, current) =>
		(_isEmpty(current) ? previous : previous + '.' + current)) + ']: ' + msg;

	return text;
};

/**
 *
 * @type {function(*=, {prefix: *, suffix?: *, caller?: *}): string}
 * @private
 */
const _DEFAULT_FORMATTER = _CONSOLE_FORMATTER;

/**
 * Get the formatter/s from the options
 *
 *
 * @param target
 * @param options
 * @returns {*}
 * @private
 */
const _getMsgFormatter = function(target, options) {
	if (options && _isFunction(options)) {
		return options;
	}

	const {formatter} = options.targets[target];

	return formatter ? formatter : _DEFAULT_FORMATTER;
};

/**
 *
 * @param target
 * @returns {*}
 * @private
 */
const _getTargetFn = function(target) {
	if (_isFunction(target)) {
		return target;
	}

	switch (target) {
		case Target.NOTIFICATION:
			return _messageToNotification;
		case Target.SERVER:
			return _messageToServer;
		case Target.CONSOLE:
			return _messageToConsole;
		default:
			return null;
	}
};

/**
 *
 * @param msg
 * @param logLevel
 * @param options
 * @private
 */
const _messageToConsole = function(msg, logLevel, options) {
	const {extra, output: {tables, objects, stringify}} = options;

	logLevel = LogLevel.parse(logLevel);
	const logger = console[logLevel.name] || console.log;

	// if to print objects or 'extra' is not an object, allow to print
	let extraOutput;
	//extraOutput = objects || !_isObject(extra) ? extra : '';
	if (extra) {
		if (_isObject(extra)) {
			extraOutput = objects ? (stringify ? JSON.stringify(extra) : extra) : HIDDEN_OBJECT_DATA;
		} else {
			extraOutput = extra;
		}
	} else {
		extraOutput = '';
	}

	logger ? logger(msg, extraOutput) : null;

	if (tables && _isArray(extra)) {
		console.table(extra);
	}
};

/**
 * TODO eds-alert
 * @param msg
 * @private
 */
const _messageToNotification = function(msg) {
	window.alert(msg);
};

/**
 * TODO
 * @param msg
 * @param logLevel
 * @param options
 * @private
 */
const _messageToServer = function(msg, logLevel, options) {
	_messageToConsole(msg, logLevel, options);
};

/**
 *
 * @param msg
 * @param logLevel
 * @param options
 * @param target
 * @param _caller
 * @private
 */
const _message = function(msg, logLevel, options, target, _caller) {
	const targetFn = _getTargetFn(target);

	if (targetFn) {
		const formatter = _getMsgFormatter(target, options);
		const formatterMsg = formatter ? formatter(msg, options) : msg;

		targetFn(formatterMsg, logLevel, options, target, _caller);
	}
};

/**
 * Default options set at start. Can be overridden by the user at the construction stage
 * or/and in each message sent.
 */
const defaultOptions = Object.freeze({
	prefix: DEFAULT_PREFIX,
	suffix: '',
	// tags
	tags: [],
	// allow and log level of each of the existing possible targets
	targets: {
		[Target.CONSOLE]: {
			allow: true,
			formatter: _CONSOLE_FORMATTER,
			logLevel: LogLevel.INFO
		},
		[Target.SERVER]: {
			allow: false,
			formatter: _SERVER_FORMATTER,
			logLevel: LogLevel.WARN
		},
		[Target.NOTIFICATION]: {
			allow: false,
			formatter: _NOTIFICATION_FORMATTER,
			logLevel: LogLevel.WARN
		}
	},

	// force logging, even if log level does not match
	force: false,
	// Allow printing output of the following types
	output: {
		//if possible, extra value will be printed as a table (if it exists and is an Array)
		tables: false,
		//if possible, extra value will be printed as an object (if it exists)
		objects: false,
		// Objects will be stringify-ied to save on memory (must also set objects=true in order to affect)
		stringify: false,
		// Allow the use of triggered logging
		triggers: false
	}
});


/**
 *
 * @param options
 * @constructor
 */
export function Logging(options = {}) {
	try {
		/**
		 * @param target
		 * @param options
		 * @returns {*}
		 */
		this.getAllow = (target, options = this.options) => {
			return target ? _get(options.targets, `${target}.allow`, false) :
				_mapValues(options.targets, target => target.allow);
		};

		/**
		 *
		 * @param allow
		 * @param targets
		 */
		this.setAllow = (allow, targets = Object.keys(Target)) => {
			targets = _isArray(targets) ? targets : [targets];
			targets.forEach(target => {
				const pT = Target.parse(target);
				if (pT) {
					const iT = this.options.targets[pT];
					iT.allow = allow;
				}
			});
		};

		/**
		 * Reset 'allow' to the the Browser window state (use default)
		 */
		this.resetAllow = () => {
			const {targets} = defaultOptions;
			if (targets) {
				const targetKeys = this.options.targets && Object.keys(this.options.targets);

				targetKeys.forEach(target => {
					this.setAllow(targets[target].allow, target);
				});
			}
		};

		/**
		 *
		 * @param target
		 * @param options
		 * @returns {*}
		 */
		this.getLogLevel = (target, options = this.options) => {
			return target ? _get(options.targets, `${target}.logLevel`, null) :
				_mapValues(options.targets, target => target.logLevel);
		};

		/**
		 *
		 * @param logLevel
		 * @param targets
		 */
		this.setLogLevel = (logLevel, targets = Object.keys(Target)) => {
			targets = _isArray(targets) ? targets : [targets];
			targets.forEach(target => {
				const pT = Target.parse(target);
				if (pT) {
					const iT = this.options.targets[pT];
					iT.logLevel = LogLevel.parse(logLevel);
				}
			});
		};

		/**
		 * Reset 'logLevel' to the the Browser window state (use default)
		 */
		this.resetLogLevel = () => {
			const {targets} = defaultOptions;
			if (targets) {
				const targetKeys = this.options.targets && Object.keys(this.options.targets);

				targetKeys.forEach(target => {
					this.setLogLevel(targets[target].logLevel, target);
				});
			}
		};

		/**
		 *
		 * @param target
		 * @param options
		 * @param logLevel
		 */
		this.canLog = (target, logLevel, options = this.options) => {
			if (this.getAllow(target, options)) {
				const targetLogLevel = this.getLogLevel(target, options);
				logLevel = LogLevel.parse(logLevel);

				return logLevel && targetLogLevel && logLevel.order >= targetLogLevel.order;
			}

			return false;
		};

		/**
		 * @param target
		 * @param options
		 * @returns {*}
		 */
		this.getFormatter = (target, options = this.options) => {
			return target ? _get(options.targets, `${target}.formatter`, null) :
				_mapValues(options.targets, target => target.formatter);
		};

		/**
		 *
		 * @param formatter
		 * @param targets
		 */
		this.setFormatter = (formatter, targets = Object.keys(Target)) => {
			targets = _isArray(targets) ? targets : [targets];
			targets.forEach(target => {
				const pT = Target.parse(target);
				if (pT) {
					const iT = this.options.targets[pT];
					iT.formatter = formatter;
				}
			});
		};

		/**
		 * Reset 'formatter' to the the Browser window state (use default)
		 */
		this.resetFormatters = () => {
			const {targets} = defaultOptions;
			if (targets) {
				const targetKeys = this.options.targets && Object.keys(this.options.targets);

				targetKeys.forEach(target => {
					this.setFormatter(targets[target].formatter, target);
				});
			}
		};

		/**
		 * Reset 'formatter' to the the Browser window state (use default)
		 */
		this.resetFormatters = () => {
			const {targets} = defaultOptions;
			if (targets) {
				const targetKeys = this.options.targets && Object.keys(this.options.targets);

				targetKeys.forEach(target => {
					this.setFormatter(targets[target].formatter, target);
				});
			}
		};

		/**
		 * Generic call to send message to targets
		 *
		 * @param msg
		 * @param logLevel
		 * @param options
		 * @param targets
		 */
		this.message = (msg, logLevel, options = {}, targets = []) => {
			const mergedOptions = {...this.options, ...options};
			const {force} = mergedOptions;

			targets = _isArray(targets) ? targets : [targets];
			msg = _isArray(msg) ? msg : [msg];

			targets.forEach(target => {
				if (force || this.canLog(target, logLevel, mergedOptions)) {

					let canContinue;

					// check tags
					const {tags, output: {triggers}} = mergedOptions;
					if (triggers) {
						if (!_isEmpty(tags)) {
							// locate tag that exists and is started
							canContinue = tags.find((tag) => {
								const _tag = this.tags[tag];

								return (_tag && _tag.started);
							});
						} else {
							// cancel, no tags but needs to use them
							canContinue = false;
						}
					} else {
						canContinue = true;
					}

					if (canContinue) {
						_message(msg, logLevel, mergedOptions, target, this);
					}
				}
			});
		};

		/**
		 * TODO Send message to server
		 *
		 * @param msg
		 * @param logLevel
		 * @param options
		 */
		this.post = (msg, logLevel, options = {}) => {
			this.message(msg, logLevel, options, [Target.SERVER]);
		};

		/**
		 * TODO Send message to notification
		 *
		 * @param msg
		 * @param logLevel
		 * @param options
		 */
		this.notify = (msg, logLevel, options = {}) => {
			this.message(msg, logLevel, options, [Target.NOTIFICATION]);
		};

		/**
		 * Send message to console logger
		 *
		 * @param msg      - Array or a String containing the message you want to print
		 * @param logLevel - The log level of this message. Should be higher or equal to
		 *                   the log level currently set in logging component.
		 * @param options  - Additional options object. Merged with the current state of
		 *                   options
		 */
		this.log = (msg, logLevel = LogLevel.INFO, options = {}) => {
			this.message(msg, logLevel, options, [Target.CONSOLE]);
		};
		/**
		 * Send messages with log level TRACE
		 * @param msg
		 * @param options
		 */
		this.trace = (msg, options = {}) => {
			this.message(msg, LogLevel.TRACE, options, [Target.CONSOLE]);
		};
		/**
		 * Send messages with log level DEBUG
		 * @param msg
		 * @param options
		 */
		this.debug = (msg, options = {}) => {
			this.message(msg, LogLevel.DEBUG, options, [Target.CONSOLE]);
		};
		/**
		 * Send messages with log level INFO
		 * @param msg
		 * @param options
		 */
		this.info = (msg, options = {}) => {
			this.message(msg, LogLevel.INFO, options, [Target.CONSOLE]);
		};
		/**
		 * Send messages with log level WARN
		 * @param msg
		 * @param options
		 */
		this.warn = (msg, options = {}) => {
			this.message(msg, LogLevel.WARN, options, [Target.CONSOLE]);
		};
		/**
		 * Send messages with log level ERROR
		 * @param msg
		 * @param options
		 */
		this.error = (msg, options = {}) => {
			this.message(msg, LogLevel.ERROR, options, [Target.CONSOLE]);
		};

		/**
		 *
		 * @param newOptions
		 */
		this.updateOptions = (...newOptions) => {
			const result = newOptions.reduce((previous, current) => {
				const updatedOptions = {...previous, ...current};

				const keys = ['targets', 'output'];
				keys.forEach(key => {
					const oldValues = _get(previous, key, {});
					const newValues = _get(current, key, {});

					updatedOptions[key] = {...oldValues, ...newValues};
				});

				return updatedOptions;
			}, {...this.options});

			this.options = result;
		};

		this.tags = {};

		// TODO under development
		this.trigger = {
			start: (tag, condition = true, options = null) => {
				if (condition) {
					this.tags[tag] = {
						started: true,
						options
					};

					this.message(['--START >>', tag], LogLevel.INFO, options);
				}
			},

			// TODO not removing tag from array... should we?
			stop: (tag, condition = true, options = null) => {
				if (condition) {
					if (this.tags[tag]) {
						this.tags[tag] = {
							started: false,
							options
						};

						this.message(['--STOP <<', tag], LogLevel.INFO, options);
					}
				}
			}
		};

	} finally {
		this.updateOptions(defaultOptions, options);
	}
}

