import {createActionConfig} from '../../createActionConfig';
import effectNocShowNotification from './effectNocShowNotification';
import effectNocClearNotification from './effectNocClearNotification';
import effectNocNotificationsUpdated from './effectNocNotificationsUpdated';

export const behaviorNocNotification = {
	name: 'behavior-noc-notification',

	properties: {
		notifications: {
			default: []
		}
	},

	actionHandlers: {
		[effectNocShowNotification.ACTION_TYPES.NOC_SHOW_NOTIFICATION]:
			createActionConfig(effectNocShowNotification.effectHandlers.showNotificationEffect),

		[effectNocClearNotification.ACTION_TYPES.NOC_CLEAR_NOTIFICATIONS]:
			createActionConfig(effectNocClearNotification.effectHandlers.clearNotificationEffect),

		[effectNocClearNotification.ACTION_TYPES.EDS_ALERT_ACTION_CLICKED]:
			createActionConfig(effectNocClearNotification.effectHandlers.edsAlertActionClicked),

		[effectNocNotificationsUpdated.ACTION_TYPES.EDS_ITEMS_SET]:
			createActionConfig(effectNocNotificationsUpdated.effectHandlers.updateNotificationsEffect)
	}
}