/**
 * Convertion of js `setTimeout` function into async function.
 * Usage: 
 * `await sleep(ms);`
 * `doStuffAfterTimeoutTriggered(); ...`
 * @param {*} ms - delay value in milliseconds. Passed as is to `setTimeout`
 */
export const sleep = async ms => 
	new Promise(resolve => setTimeout(resolve, ms));