import {Performance} from './utils/performance';
import {Logging, LogLevel} from './utils/logging';
import {Misc} from './utils/misc';

const DEV_LOG_LEVEL = LogLevel.INFO;
const PROD_LOG_LEVEL = LogLevel.WARN;

/**
 * Shorthand to performance utils (instead of calling UTILS.PERFORMANCE, simply call PERFORMANCE, etc.)
 */
export const MISC = new Misc();
export const PERFORMANCE = new Performance();
export const LOGGING = new Logging();

/**
 * Utilities for NOCpit
 */
export const UTILS = {
	/**
	 * Miscellaneous utilities
	 */
	misc: () => MISC,
	/**
	 * Logging utilities
	 */
	logging: () => LOGGING,
	/**
	 * Performance utilities
	 */
	performance: () => PERFORMANCE
};

// TODO a dispatcher like will be much better (dispatch a log message to whoever listens, register the logging util(s), call by each utility)


//
// TODO beginning code for loading debug configuration from server and overriding default
function overrideDefaults() {
	//
	// setting options (after all utilities have been created)
	//
	function loadDefaultsFromClient() {
		// TODO move each default call to the specific utility (thn override with global util.js)

		// override properties of one utility with properties that are from a different utility
		LOGGING.setLogLevel(MISC.isInDev() ? DEV_LOG_LEVEL : PROD_LOG_LEVEL);
		PERFORMANCE.setAllow(MISC.isInDev());
		PERFORMANCE.setLogCallback(LOGGING.log);
	}

	function loadDefaultsFromServer() {
		// TODO load default properties per utility from GlideProperties
	}

	loadDefaultsFromClient();
	loadDefaultsFromServer();
}

overrideDefaults();

//
// Set default window NOC (for console overrides)
//
window.NOC = {
	...window.NOC,
	MISC,
	LOGGING,
	PERFORMANCE
};
