import {createNocElement} from '../../createNocElement';
import {createActionConfig} from '../../createActionConfig';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {view} from './snOpwsLoaderView';
import snNocLoaderEffect from './snOpwsLoaderEffect';

createNocElement('sn-opws-loader', {
	renderer: {
		type: snabbdom,
		view
	},

	initialState: {
		showing: false,
		debounceFn: null
	},

	properties: {
		debounceTime: {
			default: 300
		},
		label: {
			default: null
		},
		size: {
			default: 'lg'
		}
	},

	actionHandlers: {
		[snNocLoaderEffect.ACTION_TYPES.OPWS_LOADER_BOOTSTRAP]:
			createActionConfig(snNocLoaderEffect.effectHandlers.opwsLoaderBootstrap),

		[snNocLoaderEffect.ACTION_TYPES.OPWS_LOADER_DISCONNECTED]:
			createActionConfig(snNocLoaderEffect.effectHandlers.opwsLoaderCancel),

		[snNocLoaderEffect.ACTION_TYPES.OPWS_LOADER_SHOW]:
			createActionConfig(snNocLoaderEffect.effectHandlers.opwsLoaderShow)
	}
});