import '@servicenow/now-alert';

export const view = (state) => {
	const {
		notifications = []
	} = state.properties;
	
	const notificationsJsx = (notifications && notifications.length !== 0) ?
		<now-alert-list 
			className='notifications'
			manage-items
			items={notifications}/> :
		null;

	return notificationsJsx;
};