const IN_DEV_OVERRIDE = false;

/**
 *
 * @param params
 * @constructor
 */
export function Misc(params = {}) {
	/**
	 *
	 * @type {boolean}
	 * @private
	 */
	this._inDev_override = IN_DEV_OVERRIDE;
	/**
	 *
	 * @returns {boolean}
	 */
	this.isInDev = function() {
		return process.env.NODE_ENV === 'development' || this._inDev_override;
	};
}