import moment from 'moment';
import '@servicenow/now-label-value';
import {t} from 'sn-translate';

const computeLastUpdatedInterval = lastUpdated =>
	moment(lastUpdated).fromNow();

export const view = (state) => {
	const {properties: {lastUpdated, timeAgoFormat, textItems}} = state;

	if (!lastUpdated) {
		return null;
	}

	const updatedLabel = t(timeAgoFormat, computeLastUpdatedInterval(lastUpdated), ...textItems);

	return (
		<now-label-value-inline
			className='sn-opws-time-ago now-align -block-end'
			label={updatedLabel}
		/>);
};