import {t} from 'sn-translate';
import {createRef} from '@servicenow/ui-renderer-snabbdom';
import {getFooterJsx} from './getFooterJsx';
import {getChoiceControlsJsx} from './getChoiceControlsJsx';
import {getCardJsx} from './getCardJsx';
import {getHeaderJsx} from './getHeaderJsx';
import '@servicenow/now-empty-state';
import '../sn-opws-is-visible';
import {getId} from '../../fieldGetters';
import effectOnConnect from './effectOnConnect';

const LABEL_FOOTER = t('Side panel footer');
const LABEL_NO_ITEMS = t('No records to display');

const getLoaderSection = selectedType => {
	const typeLabel = t('Loading {0}...', selectedType.label);
	return <now-empty-state 
		className='now-align -block-center loader' 
		variant='fit' 
		type='loading' 
		body-text=' ' 
		header-text={typeLabel}/>;
};

//view()
export default (state, {dispatch}) => {
	const {
		selectedSortOption,
		properties: {
			selectedItemId,
			totalRecords = 0,
			lastUpdated = 0,
			items = [],
			itemTypes = [],
			selectedType = {},
			headerTitle = '',
			isLoading,
			altFooterTextBuilder,
			showBackButton,
			hideRefreshButton
		}
	} = state;

	//DEF0083821: `isValidType` used to catch changes in itemTypes array that caused currently selectedType to disappear.
	//selectedType may be not initialised or empty at first, skipping this and resolving as valid state.
	const isValidType = !selectedType || !getId(selectedType) || itemTypes.map(getId).includes(getId(selectedType));
	
	//DEF0083821: If selectedType is no longer in the itemTypes list, need to trigger effectOnConnect to re-select default selected type.
	if (!isValidType)
		dispatch(effectOnConnect.ACTION_TYPES.RETRY_ON_CONNECT, {});


	const showLoading = isLoading || !isValidType;

	const {
		label: typeLabel,
		toolTipInfo
	} = selectedType;

	const paramsForCard = {
		selectedItemId,
		selectedType
	};
	
	const itemsList = showLoading ?
		getLoaderSection(selectedType) : (items.length === 0) ?
			<now-empty-state className='now-align -block-center loader' variant='fit' type='empty' header-text={LABEL_NO_ITEMS} body-text=' '/> :
			items.map(getCardJsx.bind(null, paramsForCard)).filter(Boolean);

	const spacerJsx = showLoading ? 
		null : 
		<div className='now-align -block-end spacer'/>;

	const headerJsx = getHeaderJsx({headerTitle, lastUpdated, dispatch, showBackButton, hideRefreshButton});
	const choiceControlsJsx = getChoiceControlsJsx({itemTypes, selectedType, selectedSortOption});

	const footerJsx = getFooterJsx({
		firstRecord: 0,
		itemsSize: items.length, 
		totalRecords,
		toolTipInfo,
		altFooterTextBuilder,
		selectedType
	}); 

	const anchorElm = createRef();

	return (
		<div className='noc-related-items-list now-p-block-start--xl'>
			<sn-opws-is-visible anchor-elm={anchorElm}/>
			<div className='now-align -block-start'>
				<div className='now-align horizontal now-p-inline--xl space-between'>
					{headerJsx}
					{choiceControlsJsx}
				</div>
			</div>
			
			<span className='cards-area-wrapper now-align -block-center'>
				<section role='feed' ref={anchorElm} label={typeLabel} className='cards-area now-p-inline--xl'>
					{itemsList}
					{spacerJsx}
				</section>
			</span>

			<footer className='now-align -block-end' aria-label={LABEL_FOOTER}>
				{footerJsx}
			</footer>
			
		</div>
	);
};