/**
 * Seismic component noc-field-select.
 *
 */
import {createNocElement} from '../../createNocElement';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './nocSearch.scss';
import {view} from './view.js';


createNocElement('noc-search', {
    renderer: {
        type: snabbdom,
        view
    },
    styles,

    properties: {
        searchTerm: {
            default: ''
        },
        searchDescription: {
            default: ''
        },
        searchLabel: {
            default: ''
        },
        clearSearchLabel: {
            default: ''
        }
    }
})
;