import effectOnConnect from './effectOnConnect';

/**
 * This effect triggers on-connect effect,
 * each time when visibility of the component on DOM changes from hidden to visible.
 */
export const effectOnVisibilityChange = coeffects => {
	const {
		action: {
			payload: {isVisible}
		},
		dispatch
	} = coeffects;

	if (isVisible)
		dispatch(effectOnConnect.ACTION_TYPES.RETRY_ON_CONNECT, {});
};