import Color from 'color';
import {reduce as _reduce} from 'lodash';
import {hexToRGB} from './shouldUseWhiteText';

/**
 * Given start and end colors, exterpolate array of colors,
 * wit hgiven brightness difference.
 * @param {*} start exterpolate from this color value.
 * @param {*} end exterpolate to this color value.
 * @param {*} difference difference in brightness relative to previous color in sequence.
 * @param {*} steps number of colors to generate.
 */
function scale(start, end, difference, steps) {
	const colors = [];
	for (let step = 0; step < steps; step++) {
		const c = Color(start).mix(Color(end), (difference * (step + 1)) / steps);
		colors.push(c.hex());
	}
	return colors;
}
/**
 * Convert hex color code to R,G,B tuple for var value.
 * @param {*} hex 
 */
function hexToRGBTriplicate(hex) {
	return hexToRGB(hex).join(', ');
}
/**
 * Generating sequence of 4 colors with different brightness,
 * based on provided base color.
 * Variations of base colors are used in intermediate element state,
 * like :hover, :active, disabled.
 * @param {*} base 
 */
function getColorSet(base) {
    return [
		base,
		...scale(base, 'white', 0.75, 3)
    ];
}

/**
 * Generate CSS string with variables for each one of provided colors.
 * @param {String} baseName 
 * @param {Array} colors 
 */
function printVar(baseName, colors) {
    return colors.reduce((acc, value, i) => `
		${acc}
		\n--em-severity-color-${baseName}-${i}: ${value};`,

        '');
}

/**
 * Generate CSS string with variables for all severity colors.
 * @param {*} colorsAndIcons 
 */
export const generateSeverityColorsVars = colorsAndIcons => _reduce(colorsAndIcons, (result, record, value) => `
	${result}
	${printVar(value, getColorSet(record.colorCode).map(hexToRGBTriplicate))}`, 
'');
