import {createNocElement} from '../../createNocElement';
import {createActionConfig} from '../../createActionConfig';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './snOpwsRelatedItemsListView';
import actionTypes from './actionTypes';
import styles from './snOpwsRelatedItemsList.scss';
import effectManageSelections from './effectManageSelections';
import effectOnConnect from './effectOnConnect';
import effectCardSelected from './effectCardSelected';
import effectRefreshRequested from './effectRefreshRequested';
import effectIsVisible from '../sn-opws-is-visible/effectIsVisible';
import {effectOnVisibilityChange} from './effectOnVisibilityChange';

/**
 * Seismic component `sn-opws-related-items-list`.
 * Displays list of item cards, with controls t oswitch between item types and choose the sortBy options.
 * Also contains manual/delayed refesh logic and pagination.
 */
createNocElement('sn-opws-related-items-list', {
	renderer: {
		type: snabbdom,
		view
	},

	styles,

	initialState: {
		firstRecord: 0, //first record to fetch, for items query offset (0-based)

	},

	properties: {
		selectedSortOption: {
			default: null
		},

		isLoading: {
			default: false
		},

		headerTitle: {
			default: ''
		},

		//total number of records for pagination display
		totalRecords: {
			default: 0
		},

		//timestamp of last updated time for the items
		lastUpdated: {
			default: 0
		},

		/* For dropdown of card/item types.
		 * Each type is an object:
		 {
			 label: string
			 id: unique string
			 [optional] icon: DS icon to display on the dropdown
			 cardComponentName: string
		 }
		 */
		itemTypes: {
			default: []
		},

		//reference to selected object on itemTypes
		selectedType: {
			default: {},
		},

		/*
		 * Mapping from item.id to item object.
		 * Each item is an object, representing DTO of a card of currently selected type.
		 */
		items: {
			default: []
		},

		/**
		 * Optional, id of selected card.
		 * Make sure you only use it together with `manageSelectedItemId=true`
		 */
		selectedItemId: {
			default: ''
		},

		/**
		 * Then this property is true, 
		 * `selectedItemId` will never be updated internally, 
		 * expecting it to be passed from outside.
		 */
		manageSelectedItemId: {
			default: false
		},

		/**
		 * Optional getter function that receives selectedType as argument and returns `String` to set as a footer text.
		 * If the function not provided, the footer text won't be modified.
		 * If the function is provided and returns a String, then the footer text will be set from the returned string, overriding the original footer.
		 * If the provided fucntion doen't return String, the footer will not be overriden (returned value is ignored).
		 * @type Function
		 */
		altFooterTextBuilder: {
			default: null
		},

		/**
		 * If 'true', then back button will appear in the list header.
		 * Clicking on the button, disaptches action 'OPWS_RELATED_ITEMS_LIST#BACK_ACTION'.
		 * @type {boolean}
		 */
		showBackButton: {
			default: false,
			schema: {type: 'boolean'}
		},

		/**
		 * If 'true', then the refresh button AND the tiem-ago lable will not appear in the list header.
		 * @type {boolean}
		 */
		hideRefreshButton: {
			default: false,
			schema: {type: 'boolean'}
		}
	},

	actionHandlers: {
		[effectOnConnect.ACTION_TYPES.RETRY_ON_CONNECT]:
			createActionConfig(effectOnConnect.effectHandlers.onConnect),

		[effectRefreshRequested.ACTION_TYPES.REFRESH_REQUESTED_INTERNAL]:
			createActionConfig(effectRefreshRequested.effectHandlers.completeRefreshAction),

		[effectManageSelections.ACTION_TYPES.ITEMS_SET]: 
			createActionConfig(effectManageSelections.effectHandlers.effectManageSelections),

		[effectCardSelected.ACTION_TYPES.CARD_SELECTED]: 
			createActionConfig(effectCardSelected.effectHandlers.cardSelected, false), 

		[effectIsVisible.ACTION_TYPES.DEFAULT_ON_CHANGE]:
			createActionConfig(effectOnVisibilityChange)
	},

	actions: {
		/**
		 * Dispatched when 'back' button is clicked on the header.
		 * Default payload is empty object.
		 * Use `append-to-payload` to add payload.
		 */
		[actionTypes.BACK_ACTION]: {}
	}
});