import {has as _has, get as _get} from 'lodash';
import {CONSTANTS} from './constants';
import {createActionConfig} from './createActionConfig';
import {LOGGING} from './utils';
import {LogLevel, Target} from './utils/logging';
import {NOTIFICATIONS} from './notifications';


//TODO before connecting this effect, we must have consistent format of error response.

const ERROR_FORMAT = {
	GQL_INSTANCE_ERROR: 'GQL_INSTANCE_ERROR',
	GQL_INVALID_ERROR: 'GQL_INVALID_ERROR',
	HTTP_ERROR: 'HTTP_ERROR',
	SERVER_ERROR: 'SERVER_ERROR'
};

const constructError = (errorType, msg, data) => ({
	errorType, 
	msg, 
	data
});

const DEFAULT_OPTIONS = {};

const DEFAULT_TARGETS = [Target.CONSOLE, Target.NOTIFICATION];

const DEFAULT_LOG_LEVEL = LogLevel.ERROR;

const DEFAULT_ERROR_FN = function({
	data, 
	dispatch, 
	logLevel = DEFAULT_LOG_LEVEL, 
	options = DEFAULT_OPTIONS, 
	targets = DEFAULT_TARGETS}) {

	const {statusCode, msg, caller} = data;

	LOGGING.message(msg, logLevel, {caller, output: {objects: true, tables: true}, extra: data, ...options}, targets);

	if (dispatch) {
		if (statusCode) {
			dispatch('GLOBAL_ERROR', {statusCode});
		} else {
			dispatch(CONSTANTS.ACTION_TYPES.NOC_SHOW_NOTIFICATION, {
				status: 'warning', //TODO Maayan: choose correct status
				icon: 'circle-exclamation-outline',
				header: NOTIFICATIONS.ERROR,
				content: NOTIFICATIONS.INTERNAL_ERROR
			});

		}
	}
};

/**
 *
 * @param payload
 * @param logLevel
 * @param options
 * @param targets
 * @param fn
 * @returns {*} null if no error was detected, else an array of Errors as found in response
 */
export const checkForError = ({
	payload = {}, 
	dispatch, 
	caller = null, 
	logLevel = DEFAULT_LOG_LEVEL, 
	options = DEFAULT_OPTIONS, 
	targets = DEFAULT_TARGETS, 
	fn = DEFAULT_ERROR_FN}) => {

	let errors;
	const {status_code: statusCode} = payload;
	
	//standartizing data of error object that can arrive from different parts of payload (GQL vs. HTTP)
	const data = {
		...(payload.result || payload.data || {}),
		statusCode,
		caller
	};

	if (_has(payload, 'error')) {
		errors = [payload.error].map(err => constructError(ERROR_FORMAT.SERVER_ERROR, err.message, err));

	} else if (_has(payload, 'errors') && payload.errors.length > 0) {
		errors = payload.errors.map(err => constructError(ERROR_FORMAT.GQL_INVALID_ERROR, err.message, err));

	} else if (_has(payload, 'result.error')) {
		errors = [constructError(ERROR_FORMAT.GQL_INSTANCE_ERROR, data.error, data)];

	} else if (_has(payload, 'data.error')) {
		errors = [constructError(ERROR_FORMAT.HTTP_ERROR, data.error.message, data)];


	}

	if (errors) {
		errors.forEach(error => fn({data: error.data, dispatch, logLevel, options, targets}));

		return errors;
	}

	return null;
};

/**
 * TODO TBD - Currently we will use the checkForError function without calling it through an effect as the effect can
 * not update the state or return a value
 *
 * @type {{effect(*): void}}
 */
const nocErrorHandler = {
	effect(coeffects) {
		const {
			dispatch,
			action: {payload}
		} = coeffects;

		//attempt to pass URL of original request as caller in error message
		const caller = _get(coeffects, 'action.meta.request.updatedUrl', 'nocErrorHandler');

		checkForError({payload, dispatch, caller});
	}
};

export const nocErrorBehavior = {
	name: 'nocErrorBehavior',
	actionHandlers: {
		[CONSTANTS.ACTION_TYPES.NOC_ERROR_OCCURRED]:
			createActionConfig(nocErrorHandler)
	}
};